.mainTextStyle {
    font-family: Lato;
    line-height: 1.2; }

.mainButtonHover {
    background-color: $mainColorWhite;
    color: $mainColorRed;
    border: 1px solid $mainColorRed;
    transition-duration: $transitionDuration; }

.secondButtonHover {
    color: $mainColorRed;
    border: 1px solid $mainColorRed;
    transition-duration: $transitionDuration; }

.hoverShadow {
    box-shadow: 0 0 15px #a4a4a4;
    transition-duration: $transitionDuration; }
