.header {
    min-height: 100px;
    display: flex;
    align-content: center;
    justify-content: space-between;

    &-right,

    &-left {
        display: flex;
        align-items: center;
        height: inherit; }

    &__search {
        display: flex;
        align-items: center;
        overflow: hidden;
        border: 1px solid #e6e6e6;
        border-radius: 3px 3px 3px 3px;
        height: 38px; }

    &__search input {
        height: inherit;
        outline: none;
        overflow: hidden;
        border: none;
        border-right: 1px solid #e6e6e6;
        border-left: 1px solid #e6e6e6;
        font-family: Lato;
        font-size: 14px;
        line-height: 32px;
        font-weight: 300;
        letter-spacing: 0.025em;
        color: #a4a4a4;
        width: 279px;
        padding-left: 18px; }

    &__search button {
        width: 35px;
        height: 40px;
        border: none;
        cursor: pointer;
        outline: 0;
