.search__browse {
    width: 87px;
    height: 38px;
    background-color: #f6f6f6; }

.dropdown__summary {
    color: #222;
    font-weight: 300; }

/*-----dropdown menu------------*/

.dropdown__summary {
    position: relative;
    height: 38px;
    display: flex;
    justify-content: space-around;
    align-items: center; }

.dropdown__menu {
    width: 262px;
    background-color: #ffffff;
    border-radius: 5px;
    z-index: 2;
    position: absolute;
    left: 200px;
    border: 1px solid #e8e8e8;

    &_list {
        padding: 15px;
        display: flex;
        flex-direction: column; } }

.dropdown_heading {
    font-family: Lato;
    font-size: 14px;
    line-height: 32px;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.025em;
    color: #232323;
    border-bottom: 1px solid #e8e8e8;
    margin-bottom: 8px; }

.dropdown__menu::before {
    content: "";
    display: block;
    width: 6px;
    height: 6px;
    background-color: #ffffff;
    position: absolute;
    top: -4px;
    left: 74px;
    transform: rotate(45deg);
    border-left: 1px solid #e8e8e8;
    border-top: 1px solid #e8e8e8; }

.dropdown__menu_item {
    font-family: Lato;
    font-size: 14px;
    line-height: 28px;
    font-weight: 400;
    letter-spacing: 0.025em;
    color: #4f4f4f;
    transition-duration: 0.3s;

    &:hover {
        color: #f16d7f;
        transition-duration: 0.3s; } }

.dropdown__summary:-webkit-details-marker {
    display: none; }

.dropdown__menu .menu-item {
    z-index: 1; }

/*---------------------------*/

