/*-----mega__menu-----*/

.menu-item {
    position: relative;
    transition-duration: 0.3s;

    &:hover .mega__menu {
        display: flex;
        z-index: 1;
        transition-duration: 0.3s; } }

.mega__menu {
    background-color: #ffffff;
    border-radius: 5px;
    border: 1px solid #e8e8e8;
    position: absolute;
    display: none;
    padding: 20px;
    top: 24px;

    &::before {
        content: "";
        display: block;
        width: 12px;
        height: 12px;
        background-color: #ffffff;
        position: absolute;
        top: -8px;
        left: 17px;
        transform: rotate(45deg);
        border-left: 1px solid #e8e8e8;
        border-top: 1px solid #e8e8e8; }

    &_column {
        margin-right: 46px; }

    &_column:last-of-type {
        margin-right: 0; } }

.column-heading {
    font-family: Lato;
    font-size: 14px;
    line-height: 32px;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.025em;
    color: #232323;
    border-bottom: 1px solid #e8e8e8;
    width: 235px;
    padding-bottom: 8px;
    margin-bottom: 8px; }

.mega__menu_item {
    display: block;
    color: #646464;
    font-family: Lato;
    font-size: 14px;
    font-weight: 400;
    line-height: 28px;
    text-decoration: none;
    text-transform: capitalize;
    transition-duration: 0.3s;

    &:hover {
        color: #f16d7f;
        transition-duration: 0.3s; } }

.mega__menu_img {
    width: 255px;
    height: 118px;
    background-image: url(img/mega_menu.png);
    align-items: flex-end;
    display: flex;
    justify-content: flex-end;
    margin-top: 15px;

    &Text {
        width: 71px;
        height: 45px;
        font-family: Lato;
        font-size: 22px;
        line-height: 28px;
        font-weight: 900;
        text-transform: uppercase;
        letter-spacing: 0.025em;
        color: #ffffff;
        margin-right: 10px;
        margin-bottom: 15px; } }

.last:hover .mega__menu {
    right: -10px;
    transition-duration: 0.3s; }

.last .mega__menu::before {
    left: 800px; }

.middle:hover .mega__menu {
    right: -400px;
    transition-duration: 0.3s; }

.middle .mega__menu::before {
    left: 430px; }

